.title {
  font-weight: 700;
  font-size: 20px;
  padding-left: 10px;
  padding-bottom: 3px;
}

.radioSection {
  button {
    cursor: pointer;
    background-color: rgba(238, 238, 238, 0.652);
    border: none;
    font-size: 16px;
    font-family: var(--main-font);
    margin-left: 20px;
    width: 80%;
    height: 32px;
    border-radius: 12px;
    margin-top: 10px;
    &:hover {
      background-color: rgb(238, 238, 238);
    }
  }
  .radioItem {
    input {
      &[type='radio'] {
        display: none;
        &:checked ~ label {
          &::before {
            opacity: 1;
            visibility: visible;
            transform: scale(1);
          }
        }
      }
    }
    label {
      font-weight: 500;
      display: inline-block;
      padding: 10px 60px;
      cursor: pointer;
      white-space: nowrap;
      position: relative;
      &::after,
      &::before {
        content: '';
        position: absolute;
        border-radius: 50%;
      }
      &::after {
        width: 20px;
        height: 20px;
        border: 3px solid var(--main-color);
        left: 20px;
        top: calc(50% - 9px);
      }
      &::before {
        background-color: var(--main-color);
        height: 10px;
        width: 10px;
        left: 25px;
        top: calc(50% - 4px);
        transform: scale(3);
        opacity: 0;
        visibility: hidden;
        transition: 0.4s ease 0s;
      }
      &:hover {
        background-color: var(--dark-20);
      }
    }
  }
}
